const messages = {
    required: ':name  requerido',
    minlength: ':name precisa ter no mnimo :min caracteres',
    maxlength: ':name precisa ter no mximo :max caracteres',
    email: ':name no  um e-mail vlido',
    min: ':name deve comear de :min',
    url: ':name no contm um link vlido'
}

export class ValidationMessage {
    static getMessage(error: string, replaceTokens: Array<any>) {
        let message = messages[error];
        const tokens = message.match(/\:[a-z]+/g);
        tokens.forEach((token, index) => {
            message = message.replace(token, replaceTokens[index]);
        });
        return message;
    }
}